.login-box {
    display: inline-block;
    position: fixed;
    margin: 0.4em;
    padding: 1em;
    color: black;
    z-index: 0;
    top: 40%;
    left: 50%;
    width: 60%;
    height: 55%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    text-align: center;

    /*Box Shadows*/
    -webkit-box-shadow: 10px 10px 38px 0px rgba(153, 153, 153, 1);
    -moz-box-shadow: 10px 10px 38px 0px rgba(153, 153, 153, 1);
    box-shadow: 10px 10px 38px 0px rgba(153, 153, 153, 1);

    background-color: white;
}

#header-box {
    margin-top: 10px;
}

#react-logo,
#lock-logo {
    width: 30px;
    height: 30px;
}

#plus {
    font-size: 20pt;
    text-align: center;
    align-content: center;
    top: -10%;
}

#content-box {
    margin-top: 20px;
}

#login {
    width: 50%;
    height: 50%;
    float: left;
}

#register {
    width: 50%;
    height: 50%;
    float: right;
}

#form {
    padding-top: 30px;
    padding-left: 10px;
    padding-right: 10px;
    text-align: left;
}

.form-control {
    width: 100%;
    padding: 12px 0px 12px 0px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
}

input::-webkit-input-placeholder {
    font-weight: italic;
}

.form-input {
    margin-top: 10px;
}

.form-submit {
    text-align: center;
}

input[type=submit] {
    
    margin-top: 20px;
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;

    /*Box Shadows*/
    -webkit-box-shadow: 10px 10px 38px 0px rgba(153, 153, 153, 1);
    -moz-box-shadow: 10px 10px 38px 0px rgba(153, 153, 153, 1);
    box-shadow: 10px 10px 38px 0px rgba(153, 153, 153, 1);
}